import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Modal, Input, Submit } from '../../tools';

class OpenModal extends Component {
  constructor (props) {
    super(props);
  }
  render() {
    return (
      <Modal title="">
        <form onSubmit={this.props.submit}>
          <input type="hidden" name="id" defaultValue={this.props.id} />
          <Input labelName="" placeholder="11:20161124059" name="expect" type="number" required />
          <Input labelName="" placeholder="1-5,0" name="code" type="number" required />
          <Submit />
        </form>
      </Modal>
    );
  }
}

OpenModal.propTypes = {
  submit: PropTypes.func.isRequired,
  id: PropTypes.number.isRequired,
}

OpenModal.defaultProps = {
}


export default connect()(OpenModal);


