import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Modal, Input, Submit, DateTimeInput, Textarea, AttachmentInput, Select } from '../../tools';
import { getConfig } from '../../../config/omg';

class AddModal extends Component {
  constructor (props) {
    super(props);
  }
  render() {
    return (
      <Modal title={this.props.update ? '' : ''}>
        <form onSubmit={this.props.submit}>
          <input type="hidden" name="id" defaultValue={this.props.item.id || ''} />
          <Input labelName="" name="user_name" type="text" required defaultValue={this.props.item.user_name || ''} />
          <input type="hidden" name="award_type" value="3" />
          <Input labelName="ID" name="award_id" type="number" placeholder="id,0" required defaultValue={this.props.item.award_id || ''} />
          <Input labelName="" name="total_money" type="number" required defaultValue={this.props.item.total_money || ''} />
          <Input labelName="" name="total_num" type="number" required defaultValue={this.props.item.total_num || ''} />
          <Input labelName="" name="min" type="number" required defaultValue={this.props.item.min || 1} />
          <Input labelName="" name="max" type="number" required defaultValue={this.props.item.max || ''} />
          <DateTimeInput required labelName="" name="start_time" defaultValue={this.props.item.start_time} />
          <DateTimeInput required labelName="" name="end_time" defaultValue={this.props.item.end_time} />
          <Textarea labelName="" name="blessing" defaultValue={this.props.item.blessing || ''} />
          <Submit />
        </form>
      </Modal>
    );
  }
}

AddModal.propTypes = {
  submit: PropTypes.func.isRequired,
  update: PropTypes.bool,
  item: PropTypes.obj,
}

AddModal.defaultProps = {
  item: {},
  update: false,
}

export default connect()(AddModal);


