import React, { PropTypes, Component } from 'react';
import { connect } from 'react-redux';
import { Modal, Input, Submit, DateTimeInput, Textarea, AttachmentInput, Select } from '../../tools';
import { getConfig } from '../../../config/omg';

class AddModal extends Component {
  constructor (props) {
    super(props);
  }
  render() {
    return (
      <Modal title={this.props.update ? '' : ''}>
        <form onSubmit={this.props.submit}>
          <input type="hidden" name="id" defaultValue={this.props.item.id || ''} />
          <input type="hidden" name="groups" defaultValue={this.props.item.groups || 'default'} />
          <Input labelName="" name="integral" required defaultValue={this.props.item.integral || ''} />
          <Input labelName="" name="total_quantity" type="number" required defaultValue={this.props.item.total_quantity || ''} />
          <Input labelName="" name="user_quantity" type="number" required defaultValue={this.props.item.user_quantity || 0} />
          <Select labelName="" name="award_type" options={getConfig('awardTypes')} defaultValue={this.props.item.award_type || 1} />
          <Input labelName="ID" name="award_id" type="number" required defaultValue={this.props.item.award_id || ''} />
          <DateTimeInput required limit labelName="" name="start_time" defaultValue={this.props.item.start_time} />
          <DateTimeInput required limit labelName="" name="end_time" defaultValue={this.props.item.end_time} />
          <AttachmentInput labelName="" name="photo"  position={`banner_${this.props.item.photo}`} defaultValue={this.props.item.photo} />
          <AttachmentInput labelName="()" name="photo_min"  position={`banner_${this.props.item.photo}`} defaultValue={this.props.item.photo_min} />
          <Textarea labelName="" name="desc" defaultValue={this.props.item.desc || ''} />
          <Submit />
        </form>
      </Modal>
    );
  }
}

AddModal.propTypes = {
  submit: PropTypes.func.isRequired,
  update: PropTypes.bool,
  item: PropTypes.obj,
}

AddModal.defaultProps = {
  item: {},
  update: false,
}


export default connect()(AddModal);



